
































































import Vue from "vue";
import { Component } from "vue-property-decorator";

let config = {};
let originalConfig = {};
@Component
export default class InitConfig extends Vue {
        mysql = {
            host: "",
            user: "",
            password: "",
            port: "",
            database: ""
        };

        salt = "";
        session_secret = "";
        judger = {
            address: "",
            oj_home: ""
        };

        etc = {
            compile_arguments: ""
        }

        problem_upload_dest = {
            dir: ""
        }

        mounted () {
            this.axios.get("/api/init/firstrun/")
                .then(({ data }) => {
                    Object.assign(this, data.data);
                    config = data.data;
                    originalConfig = JSON.parse(JSON.stringify(originalConfig));
                });
        }

        updateConfig () {
            Object.assign(config, this.$data);
            this.axios.post("/api/init/firstrun/config", { content: config })
                .then(({ data }) => {
                    this.$emit("configUpdated");
                });
        }

        resetConfig () {
            Object.assign(this, originalConfig);
        }
}
